var util = require('@/common/util.js');
export default {
	data() {
		return {
			code: '',
			date: '',
			endDate: '',
			selected: [{name: '入学时间'}],
			user_name: '',
			sex: 1,
			college: '',
			start_time: '2016-08',
			end_time: '',
			test: 'test',
			sexList: [{
					value: '1',
					name: '男'
				},
				{
					value: '2',
					name: '女',
					checked: 'true'
				}]
		}
	},
	methods: {
		open() {
			this.$refs.calendar.open()
		},
		open2 () {
			this.$refs.calendar2.open()
		},
		change(e) {
			this.start_time = e.fulldate
		},
		confirm(e) {
			this.start_time = e.fulldate
		},
		change2(e) {
			this.end_time = e.fulldate
		},
		confirm2(e) {
			this.end_time = e.fulldate
		},
		startDateChange(e) {
			this.start_time = e.detail.value
		},
		
		endDateChange(e) {
			this.end_time = e.detail.value
		},
		changeSex(e) {
			this.sex = e.detail.value
		},
		radioChange(evt) {
			for (let i = 0; i < this.sexList.length; i++) {
				if (this.sexList[i].value === evt.target.value) {
					this.sex = i;
					break;
				}
			}
		},
		saveUserInfo() {
			if (!this.user_name) {
				uni.showToast({
				    icon: 'none',
				    title: '请填写姓名'
				});
				return;
			}
			
			if (!this.sex) {
				uni.showToast({
				    icon: 'none',
				    title: '请选择性别'
				});
				return;
			}
			
			if (!this.college) {
				uni.showToast({
				    icon: 'none',
				    title: '请填写学校名称'
				});
				return;
			}
			
			if (!this.start_time) {
				uni.showToast({
				    icon: 'none',
				    title: '请选择入学时间'
				});
				return;
			}
			
			let endTime = ''
			if (this.end_time) {
				endTime = util.format(this.end_time)
			}
			
			this.$api.updateUserInfo({
				user_name:this.user_name,
				sex:this.sex,
				college:this.college,
				start_time: util.format(this.start_time),
				end_time: endTime
			}).then(res => {
				uni.showToast({
				    icon: 'none',
				    title: '保存成功'
				});
				this.$store.dispatch('getuserinfo')
			   setTimeout(function(){
			   	uni.switchTab({
			   	    url: '/pages/home/home'
			   	});
			   },500) 
			   
			}).catch(err => {
			})
		}
	}
}
